/* SCSS du body*/
body {
  overflow: hidden;
  height: 100vh;
  background: linear-gradient(rgba(2, 2, 2, 0.678), rgba(7, 7, 7, 0.336)),
    url("../assets/bg-planet.jpg");
  animation: bgMove 50000ms linear infinite;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  backdrop-filter: blur(5px);
}

* {
  text-align: center;
  margin: 0;
  font-family: "Chewy", cursive;
}
